"use client";
import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { toast } from "react-hot-toast";
import Navbar from "@/components/Navbar";

export default function ProviderDashboard() {
  const API_BASE = process.env.NEXT_PUBLIC_API_URL || "https://khedme-api.onrender.com";
  const [provider, setProvider] = useState(null);
  const [loading, setLoading] = useState(false);
  const [applications, setApplications] = useState([]);
  const [loadingApps, setLoadingApps] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [editData, setEditData] = useState({ full_name: "", phone: "", category_id: "", district_id: "" });

  // ‚úÖ Load provider data
  useEffect(() => {
    const stored = localStorage.getItem("provider");
    if (stored) {
      const parsed = JSON.parse(stored);
      setProvider(parsed);
      setEditData({
        full_name: parsed.full_name || "",
        phone: parsed.phone || "",
        category_id: parsed.category_id || "",
        district_id: parsed.district_id || "",
      });
    }
  }, []);

  // ‚úÖ Toggle availability
  const toggleAvailability = async () => {
    if (!provider) return toast.error("Provider not found");

    const newStatus = !provider.available;
    setLoading(true);

    try {
      const res = await fetch(`${API_BASE}/api/providers/availability`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          provider_id: provider.id,
          is_available: newStatus,
        }),
      });

      const data = await res.json();

      if (data.success) {
        toast.success(data.message);
        const updated = { ...provider, available: newStatus };
        localStorage.setItem("provider", JSON.stringify(updated));
        setProvider(updated);
        window.dispatchEvent(new Event("providerUpdated"));
      } else {
        toast.error(data.message || "Failed to update status");
      }
    } catch (error) {
      console.error("‚ùå Toggle availability error:", error);
      toast.error("Server error updating availability");
    } finally {
      setLoading(false);
    }
  };

  // ‚úÖ Fetch My Applications
  const fetchApplications = async () => {
    if (!provider?.id) return;
    setLoadingApps(true);
    try {
      const res = await fetch(`${API_BASE}/api/providers/applications/${provider.id}`);
      const data = await res.json();
      if (data.success) {
        setApplications(data.applications);
      } else {
        toast.error("Failed to load applications");
      }
    } catch (err) {
      console.error("‚ùå Error fetching applications:", err);
      toast.error("Error loading applications");
    } finally {
      setLoadingApps(false);
    }
  };

  useEffect(() => {
    if (provider?.id) fetchApplications();
  }, [provider]);

  // ‚úÖ Handle profile save
  const handleSaveProfile = async () => {
    if (!provider) return;
    setLoading(true);
    try {
      const res = await fetch(`${API_BASE}/api/providers/update`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          id: provider.id,
          ...editData,
        }),
      });

      const data = await res.json();
      if (data.success) {
        toast.success("Profile updated successfully ‚úÖ");
        localStorage.setItem("provider", JSON.stringify(data.provider));
        setProvider(data.provider);
        setShowModal(false);
      } else {
        toast.error(data.message || "Failed to update profile");
      }
    } catch (err) {
      console.error("‚ùå Profile update error:", err);
      toast.error("Server error updating profile");
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      <Navbar />
      <div className="max-w-4xl mx-auto mt-10 p-6 bg-white shadow-md rounded-2xl border border-gray-100">
        <h1 className="text-2xl font-bold text-gray-800 mb-4">Provider Dashboard</h1>

        {provider ? (
          <>
            <div className="flex justify-between items-center mb-4">
              <div>
                <p className="text-gray-600 mb-1">
                  Welcome, <span className="text-orange-600 font-semibold">{provider.full_name}</span>
                </p>
                <p className="text-sm text-gray-500">Phone: {provider.phone}</p>
              </div>

              <Button
                onClick={() => setShowModal(true)}
                className="bg-orange-500 hover:bg-orange-600 text-white px-4 py-1 text-sm"
              >
                ‚úèÔ∏è Edit Profile
              </Button>
            </div>

            {/* üîò Availability Toggle */}
            <Button
              onClick={toggleAvailability}
              disabled={loading}
              className={`px-6 py-2 rounded-full font-semibold transition-all duration-300 ${
                provider.available
                  ? "bg-green-500 hover:bg-green-600 text-white"
                  : "bg-gray-400 hover:bg-gray-500 text-white"
              }`}
            >
              {loading
                ? "Updating..."
                : provider.available
                ? "Available ‚úÖ"
                : "Unavailable ‚ùå"}
            </Button>

            {/* üßæ My Applications */}
            <div className="mt-10">
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-xl font-semibold text-gray-800">My Applications</h2>
                <Button
                  onClick={fetchApplications}
                  disabled={loadingApps}
                  className="bg-orange-500 hover:bg-orange-600 text-white px-3 py-1"
                >
                  {loadingApps ? "Refreshing..." : "üîÑ Refresh"}
                </Button>
              </div>

              {loadingApps ? (
                <p className="text-gray-500">Loading applications...</p>
              ) : applications.length === 0 ? (
                <p className="text-gray-500">No job applications found.</p>
              ) : (
                <div className="grid gap-4">
                  {applications.map((app) => (
                    <div
                      key={app.application_id}
                      className="border border-orange-100 rounded-xl p-4 shadow-sm hover:shadow-md transition"
                    >
                      <div className="flex justify-between items-center mb-2">
                        <h3 className="font-semibold text-gray-800">{app.service}</h3>
                        <span
                          className={`text-sm px-3 py-1 rounded-full font-medium ${
                            app.status === "approved"
                              ? "bg-green-100 text-green-700"
                              : app.status === "rejected"
                              ? "bg-red-100 text-red-700"
                              : "bg-yellow-100 text-yellow-700"
                          }`}
                        >
                          {app.status}
                        </span>
                      </div>
                      <p className="text-gray-700 text-sm mb-2">{app.description}</p>
                      <p className="text-gray-500 text-xs">
                        District: {app.district} ‚Ä¢ Budget: ${app.budget}
                      </p>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </>
        ) : (
          <p className="text-gray-500 text-center">No provider data found.</p>
        )}
      </div>

      {/* ü™ü Edit Profile Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
          <div className="bg-white rounded-xl shadow-lg p-6 w-full max-w-md">
            <h2 className="text-xl font-semibold text-gray-800 mb-4">Edit Profile</h2>

            <div className="space-y-3">
              <input
                type="text"
                placeholder="Full Name"
                value={editData.full_name}
                onChange={(e) => setEditData({ ...editData, full_name: e.target.value })}
                className="w-full border rounded-lg px-3 py-2"
              />
              <input
                type="text"
                placeholder="Phone Number"
                value={editData.phone}
                onChange={(e) => setEditData({ ...editData, phone: e.target.value })}
                className="w-full border rounded-lg px-3 py-2"
              />
              <input
                type="number"
                placeholder="Category ID"
                value={editData.category_id}
                onChange={(e) => setEditData({ ...editData, category_id: e.target.value })}
                className="w-full border rounded-lg px-3 py-2"
              />
              <input
                type="number"
                placeholder="District ID"
                value={editData.district_id}
                onChange={(e) => setEditData({ ...editData, district_id: e.target.value })}
                className="w-full border rounded-lg px-3 py-2"
              />
            </div>

            <div className="flex justify-end gap-3 mt-5">
              <Button
                onClick={() => setShowModal(false)}
                className="bg-gray-300 hover:bg-gray-400 text-black"
              >
                Cancel
              </Button>
              <Button
                onClick={handleSaveProfile}
                className="bg-orange-500 hover:bg-orange-600 text-white"
                disabled={loading}
              >
                {loading ? "Saving..." : "Save Changes"}
              </Button>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
